// uni-app
/*
*router       String  
*data/param   Object  
*methods      String  
*type         Number   0  1  2 3
*bMsg         String  
*eMsg		  String  
*errMsg       String  
*back		  Boolean 
*backTime	  Number  
*hideLoading  Boolean 
*useToken     Boolean token
*hideError    Boolean 
*/
import enviroment from "./enviroment.js"
let loginModelFlag = false; //

export default class Request {
	http (request) {
		
        let path = enviroment.getUrl();
		request.backTime = request.backTime == undefined ? 1000 : request.backTime;
        if(request.useToken == undefined)request.useToken = true;

		if(request.type == 1 || request.type == 3){
			uni.showLoading({title:request.bMsg != undefined ? request.bMsg : request.back ? "" : ""  ,mask:true})
		}

        request.header = {};
		request.header["Content-Type"] = request.data ? "application/json;charset=UTF-8" : 'application/x-www-form-urlencoded';

        request.data = request.data || request.param || {}
		if(uni.getStorageSync("token") )request.data.user_token =  uni.getStorageSync("token");
        for(let key in request.data){
            if(!request.data[key] && !request.data[key] === 0)delete request.data[key]
        }
		// promise
		return new Promise((resolve,reject) => {
			// 
			uni.request({
				url: `${path}${request.url}`,
				data: request.data,
				header:request.header,
				method:request.methods,
				success: (resonpes) => {
					request.resonpes = resonpes.data;
					console.log("***********************************************")
					request.url = `${path}${request.url}`;
					console.log(request)
					if(request.hideLoading !== false || request.type !== 0){uni.hideLoading();}
					// 
					console.log('',resonpes.data.status)
					if(resonpes.data.status == 200 ){
						resolve(resonpes.data.data)
						console.log(request.url)
						if(request.url == `${path}/api/login/smsLogin` || request.url == `${path}/escort/login/smsLogin` || request.url == `${path}/api/login/updateWechatByMobile` || request.url == `${path}/escort/Login/updateWechatByMobile` || request.url == `${path}/api/login/weixinLogin` || request.url == `${path}/escort/Login/weixinLogin`){
							console.log("token",resonpes.data.token)
							uni.setStorageSync("token",resonpes.data.token)
						}
						if(request.type == 2 || request.type == 3){
							if(request.back){
								uni.showToast({
									title:request.eMsg || "",
									icon:"none",
									mask:true,
									duration:request.backTime,
									success(res) {
										setTimeout(function(){
											uni.navigateBack({delta:request.delta || 1})
										},request.backTime)
									}
								})
							}else{
								uni.showToast({title:request.eMsg || "",icon:"none"})
							}
						}
					}else if(resonpes.data.status == '201'){
						resolve(resonpes.data)
						if(resonpes.data.msg == 'token') {
							if(loginModelFlag){
								return
							}
							loginModelFlag = true
							uni.showModal({
								title:"",
								content:"",
								showCancel:false,
								confirmText:"",
								success: (model) => {
									loginModelFlag = false
									if(model.confirm){
										uni.setStorageSync("token",null);
										uni.setStorageSync("hasLogin",null);
										uni.navigateTo({
											url:'/aUserPages/login/login'
										})
									}
								}
							})
							uni.hideToast()
						}else {
							uni.showToast({title:resonpes.data.msg,icon:"none"})
						}
					}else if(resonpes.data.status == 401 || resonpes.data.msg == 'token' || resonpes.data.msg == 'token'){
						if(!uni.getStorageSync("hasLogin") || resonpes.data.msg == 'token'){
							if(loginModelFlag){
								return
							}
							loginModelFlag = true
							uni.setStorageSync("hasLogin",true);
							uni.showModal({
								title:"",
								content:"",
								showCancel:false,
								confirmText:"",
								success: (model) => {
									loginModelFlag = false
									console.log(model.confirm);
									if(model.confirm){
										uni.setStorageSync("token",null);
										uni.setStorageSync("hasLogin",null);
										uni.navigateTo({
											url:'/aUserPages/login/login'
										})
									}
								}
							})
						}else{
							reject(resonpes.data)
						}
					}else{
                        reject(resonpes.data)
						if(request.hideError)uni.showToast({title:request.errMsg || resonpes.data.msg,icon:"none"})
					}
				},
				fail:(error) =>{
                    console.log( `${path}${request.url}`)
					uni.hideLoading();
					reject(error)
					uni.showToast({title: '~',icon:"none"});
				}
			})
		})
	}
}
